.loading{
    width: 100vw;
    height: 100vh;
    display:grid;
    place-items: center;
    max-width: 100%;
}
.loading>div{
    width: 10vmax;
    height: 10vmax;
    border-bottom: 5px solid grey;
    border-radius: 50%;
    animation: loadingRotate 800ms linear infinite;
}
@keyframes loadingRotate{
    to{
        transform: rotate(-360deg);
    }
}